@media (min-width: 1895px) {
  .Hues {
    width: 85%;
    margin: 0 auto;
  }
}

@media (min-width: 2330px) {
  .Hues {
    width: 76%;
    margin: 0 auto;
  }
}

@media (min-width: 3770px) {
  .Hues {
    width: 62%;
    margin: 0 auto;
  }
}
/* Hue styles */
.Hue {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(100% - 2.24rem);
          flex-basis: calc(100% - 2.24rem);
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 1.12rem;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2),
              0px 2px 2px 0px rgba(0,0,0,0.14),
              0px 3px 1px -2px rgba(0,0,0,0.12);
  height: 100%;
}

@media (min-width: 550px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(50% - 2.24rem);
            flex-basis: calc(50% - 2.24rem);
  }
}

@media (min-width: 890px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(33.33% - 2.24rem);
            flex-basis: calc(33.33% - 2.24rem);
  }
}

@media (min-width: 1171px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(25% - 2.24rem);
            flex-basis: calc(25% - 2.24rem);
  }
}

@media (min-width: 1440px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(20% - 2.24rem);
            flex-basis: calc(20% - 2.24rem);
  }
}

@media (min-width: 2330px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(20% - 2.24rem);
            flex-basis: calc(20% - 2.24rem);
  }
}

@media (min-width: 3770px) {
  .Hue {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(16.66% - 2.24rem);
            flex-basis: calc(16.66% - 2.24rem);
  }
}
/* Shade styles */
.Hue_Shade {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0.87rem;
  border: 1px solid rgba(0, 0, 0, 0.12);
  margin-top: -1px;
  cursor: copy;
}

.Hue_Shade:first-child {
  margin-top: 0px;
}

.Hue_Shade--light {
  color: rgba(255, 255, 255, 0.87);
}

.Hue_Shade--dark {
  color: rgba(0, 0, 0, 0.87);
}

/* Name styles */
.Hue_Shade_Name {
  font-size: 1rem;         
}
/* Hex styles */
.Hue_Shade_Hex {
  font-family: 'Roboto Mono', 'Roboto', "Courier New", Courier, "Lucida Console", Monaco, monospace;
  font-weight: 500;
  font-size: .91rem;
  letter-spacing: 0.02rem;
  text-align: right;
}
/* Footer styles */
footer {
  margin: 1.12rem;
  padding: 1.12rem;
  -webkit-justify-content: center;
          justify-content: center;
}

footer ul {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

footer ul li {
  display: -webkit-inline-flex;
  display: inline-flex;
  font-weight: 500;
  font-family: 'Roboto Mono';
  font-size: .875rem;
  padding: 1.12rem;
}

footer a {
  color: #90a4ae;
  text-decoration: none;
}
